<template>
  <aside
    class="star-container"
    :itemtype="hasRating ? 'https://schema.org/Rating' : undefined"
    :itemscope="hasRating"
  >
    <p
      v-if="hasValorations"
      class="valoration-text"
    >
      {{ valorationValue || $t('default') }}
    </p>
    <span
      :class="{hovering: hovering !== value}"
      class="stars"
      @mouseout="hovering = value"
    >
      <template v-if="hasRating">
        <meta itemprop="worstRating" content="1">
        <meta itemprop="bestRating" content="5">
      </template>
      <span
        v-for="(n, index) in 5"
        :key="index"
        :class="{ 'each-star is-clickable': hasValorations} && isRTL ? 'rtl-star' : 'each-star'"
      >
        <img
          v-if="(hovering-n)>=0"
          class="mx-auto"
          src="./full-star.svg"
          is-ltr
          alt="star"
          @mouseover="hovering = editable ? n : hovering"
          @click="send(hovering)"
        >
        <img
          v-else-if="((hovering-n)>-1) && ((hovering-n)<0)"
          class="mx-auto half-star"
          src="./half-star.svg"
          is-ltr
          alt="star"
          @mouseover="hovering = editable ? n : hovering"
          @click="send(hovering)"
        >
        <img
          v-else-if="(hovering-n)<0"
          class="mx-auto"
          src="./empty-star.svg"
          is-ltr
          alt="star"
          @mouseover="hovering = editable ? n : hovering"
          @click="send(hovering)"
        >
      </span>
      <template v-if="hasRating">
        <meta itemprop="ratingValue" :content="value">
      </template>
    </span>
  </aside>
</template>

<i18n>
{
  "es-AR": {
    "default": "Sin valorar",
    "absolutely_no": "Absolutamente no",
    "undesirable": "Poco recomendable",
    "recommendable": "Recomendable",
    "strongly_advisable": "Bastante recomendable",
    "highly_recommended": "Muy recomendable"
  },
  "en-GB": {
    "default": "With out evaluation",
    "absolutely_no": "Absolutely not recommended",
    "undesirable": "Not recommended",
    "recommendable": "Recommendable",
    "strongly_advisable": "Slightly recommended",
    "highly_recommended": "Highly recommended"
  },
  "en-US": {
    "default": "With out evaluation",
    "absolutely_no": "Absolutely not recommended",
    "undesirable": "Not recommended",
    "recommendable": "Recommendable",
    "strongly_advisable": "Slightly recommended",
    "highly_recommended": "Highly recommended"
  },
  "es-CL": {
    "default": "Sin valorar",
    "absolutely_no": "Absolutamente no",
    "undesirable": "Poco recomendable",
    "recommendable": "Recomendable",
    "strongly_advisable": "Bastante recomendable",
    "highly_recommended": "Muy recomendable"
  },
  "es-CO": {
    "default": "Sin valorar",
    "absolutely_no": "Absolutamente no",
    "undesirable": "Poco recomendable",
    "recommendable": "Recomendable",
    "strongly_advisable": "Bastante recomendable",
    "highly_recommended": "Muy recomendable"
  },
  "ar-SA": {
    "default": " ",
    "absolutely_no": "   ",
    "undesirable": "  ",
    "recommendable": " ",
    "strongly_advisable": "    ",
    "highly_recommended": "  "
  }
}
</i18n>

<script>
const DEFAULT_LOCALE = 'en'
const AVAILABLE_LOCALES = ['es', 'en', 'sa']

export default {
  name: 'Stars',
  props: {
    hasValorations: {
      type: Boolean,
      default: false
    },
    valorationText: {
      type: [Array, String],
      default: () => ([])
    },
    value: {
      type: [Number, String],
      default: 0
    },
    hasRating: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      hovering: this.value,
      isReview: true
    }
  },
  computed: {
    locale () {
      const browserLocale = (navigator.language || navigator.userLanguage).slice(0, 2)
      return AVAILABLE_LOCALES.includes(browserLocale) ? browserLocale : DEFAULT_LOCALE
    },
    isRTL () {
      return this.locale === 'sa'
    },
    editable () {
      return typeof (this._events.input) !== 'undefined' && !!this._events.input.length
    },
    valorationValue () {
      const defaultValorations = [
        this.$t('absolutely_no'),
        this.$t('undesirable'),
        this.$t('recommendable'),
        this.$t('strongly_advisable'),
        this.$t('highly_recommended')
      ]
      if (this.hasValorations && this.valorationText !== []) {
        return defaultValorations[this.value - 1]
      } else {
        return this.valorationText[this.value - 1]
      }
    }
  },
  watch: {
    value () {
      this.hovering = this.value
    }
  },
  methods: {
    send (n) {
      for (const i in this._events.input) {
        this._events.input[i].fns(n)
      }
    }
  }
}
</script>

<style scoped lang="scss">
  .star-container {
    .valoration-text {
      width: 100%;
      text-align: left
    }
    .stars {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      .rtl-star {
        display: flex;
        align-items: center;
        -moz-transform: scaleX(-1);
        -o-transform: scaleX(-1);
        -webkit-transform: scaleX(-1);
        transform: scaleX(-1);
        &:not(:last-of-type){
          margin-left: .1rem
        }
      }
      .each-star {
        display: flex;
        align-items: center;
        &:not(:last-of-type){
          margin-right: .1rem
        }
      }
    }
  }
</style>
