<template>
  <div class="review-list">
    <div
      v-for="(item, key) in reviewsToShow"
      :key="key"
      class="review-list__item-container"
    >
      <ReviewListItem
        :initials="item.patient_initials"
        :stars="stars(item)"
        :comment="item.comment"
        :date-comment="item.created_at"
      />
    </div>
    <div
      v-if="showViewMore"
      class="review-list__item-container"
    >
      <slot name="button" />
    </div>
  </div>
</template>

<script>
import ReviewListItem from './ReviewListItem.vue'

const REVIEWS_COUNT = 2

export default {
  components: {
    ReviewListItem
  },
  props: {
    reviews: {
      type: Array,
      default: () => []
    }
  },
  computed: {
    reviewsToShow () {
      return this.reviews.slice(0, REVIEWS_COUNT)
    },
    showViewMore () {
      return this.reviews.length > REVIEWS_COUNT
    }
  },
  methods: {
    stars (review) {
      return parseInt(
        (review.consulting_wait + review.crew_treat + review.doctor_treat + review.installations + review.recommend) / 5
      )
    }
  }
}
</script>

<style lang="scss" scoped>
@import "global";

.review-list {
  text-align: initial;

  &__item-container {
    padding: 8px 15px;
  }
}
</style>
