<template>
  <div class="review-list-item">
    <div class="review-list-item__stars">
      <Stars
        :value="stars"
        has-rating
      />
      <div class="verified">
        {{ locales.verified_review }}
        <span>
          <img src="./like.svg" width="22" >
        </span>
      </div>
    </div>
    <div
      v-if="comment"
      class="review-list-item__comment is-relative"
    >
      <img src="./quotes.svg" class="is-icon-quotes" >
      <p>{{ comment }}</p>
      <span class="review-list-item__initials">{{ initials }}</span>
      <span>{{ dateComment | dateFns('L') }}</span>
    </div>
  </div>
</template>

<script>
import locales from '../shared/locales'
import Stars from './Stars'
const DEFAULT_LOCALE = 'en'
const AVAILABLE_LOCALES = ['es', 'en']
export default {
  name: 'ReviewsReviewListItem',
  components: {
    Stars
  },
  props: {
    initials: {
      type: String,
      required: true
    },
    stars: {
      type: Number,
      required: true
    },
    comment: {
      type: String,
      default: ''
    },
    dateComment: {
      type: String,
      default: ''
    }
  },
  computed: {
    locale () {
      const browserLocale = (navigator.language || navigator.userLanguage).slice(0, 2)
      return AVAILABLE_LOCALES.includes(browserLocale) ? browserLocale : DEFAULT_LOCALE
    },
    locales () {
      return locales[this.locale]
    }
  }
}
</script>

