<template>
  <div
    v-if="!loading && !providerError"
    class="widget"
  >
    <div class="widget__container">
      <div class="widget__header">
        <div>
          <article class="info-profile">
            <div class="outer photo-dr rounded">
              <div
                class="inner"
                :style="{
                  backgroundImage: `url(${storage}/provider/${provider.id}/image/profile/thumb/${provider.profileimg})`
                }"
              />
            </div>
            <div class="ml-2">
              <div class="info-doctor has-text-grey-dark">
                <div class="provider-title" type="doctor" data-cy="provider-title">
                  <span itemprop="name" class="h2 has-text-info title-provider">
                    {{ fullNameProvider }}
                  </span>
                  <span class="doctor-spec">
                    {{ upperFirst(specialtyNameOfDoctor) }}
                  </span>
                </div>
                <div v-if="provider.avg_rating" class="flex-justify-start flex-align-center">
                  <span class="is-size-7 points">
                    {{ provider.avg_rating.toFixed(1) }}
                  </span>
                  <Stars v-if="provider.avg_rating.toFixed(1)" :value="provider.avg_rating.toFixed(1)" />
                </div>
              </div>
            </div>
          </article>
        </div>
        <a
          v-if="provider.avg_rating"
          class="button is-primary is-fullwidth flex-align-center is-justify-content-space-between"
          :href="`${providerURL}`"
          target="_blank"
        >
          <Stars v-if="provider.avg_rating.toFixed(1)" :value="provider.avg_rating.toFixed(1)" />
          <div class="is-size-9 has-text-weight-semibold">
            {{ locales.review_appointment }}
          </div>
          <div class="flex-align-center">
            <span>
              <img src="./chevron-right.svg" width="18" >
            </span>
          </div>
        </a>
      </div>
      <div
        class="widget__body"
      >
        <div v-if="reviews.length > 0">
          <ReviewList :reviews="reviews">
            <template v-slot:button>
              <div class="mt-1 all-reviews">
                <a
                  class="is-info"
                  :href="`${providerURL}#reviews`"
                  target="_blank"
                >
                  {{ locales.ver_mas }} ({{ provider.review_count }})
                </a>
              </div>
            </template>
          </ReviewList>
        </div>
        <div v-else>
          <span class="no_reviews">{{ locales.no_reviews }}</span>
        </div>
      </div>
      <div class="widget__footer">
        <img src="./td-member.png" width="220">
      </div>
    </div>
  </div>
</template>

<script>

import axios from 'axios'
import locales from '../shared/locales'
import ReviewList from './ReviewList.vue'
import Stars from './Stars.vue'
export const empty = value => Array.isArray(value)
  ? value.length === 0
  : true // if its not an array return always that it is empty
const DEFAULT_LOCALE = 'en'
const AVAILABLE_LOCALES = ['es', 'en']

export default {
  components: {
    ReviewList,
    Stars
  },
  props: {
    apiKey: {
      type: String,
      required: true
    },
    region: {
      type: String,
      required: true
    },
    widgetType: {
      type: Number,
      required: true
    },
    environment: {
      type: String,
      required: true
    },
    storage: {
      type: String,
      required: true
    }
  },
  data: () => ({
    reviews: [],
    reviewsTotal: 0,
    loading: false,
    providerError: false,
    provider: undefined,
    baseProviderURL: undefined
  }),
  async created () {
    axios.defaults.baseURL = `https://${this.apiEndpoint}.topdoctors.es`
    axios.defaults.headers.common = {
      'X-Region': this.region
    }
    this.loading = true
    await this.$_fetchProvider()
    if (!this.providerError) {
      const { data } = await axios.get(`/appointments/review/provider/${this.provider.id}`, {
        params: {
          limit: 4,
          offset: 0
        }
      })
      this.reviews = data.result
    }
    this.loading = false
  },
  computed: {
    apiEndpoint () {
      return this.environment === 'localhost' ? 'apidev' : `api${this.environment}`
    },
    providerURL () {
      const slugB64 = window.btoa(this.provider.slug)
      return `${this.baseProviderURL}/reviewme/${slugB64}`
    },
    locale () {
      const browserLocale = (navigator.language || navigator.userLanguage).slice(0, 2)
      return AVAILABLE_LOCALES.includes(browserLocale) ? browserLocale : DEFAULT_LOCALE
    },
    locales () {
      return locales[this.locale]
    },
    fullNameProvider () {
      if (!this.provider) { return '' }
      return (this.provider.prefix ? this.provider.prefix : '') +
        (this.provider.name ? ' ' + this.provider.name : '') +
        (this.provider.surname ? ' ' + this.provider.surname : '')
    },
    mainSpecialty () {
      if (!this.provider || empty(this.provider.specialties)) { return }
      return this.provider.specialties.find(s => s.highlighted) || this.provider.specialties[0]
    },
    specialtyNameOfDoctor () {
      if (this.provider.specialties && this.provider.specialties.length > 0) {
        return (
          (this.provider.sex !== 'F')
            ? this.mainSpecialty.name_specialist_male
            : this.mainSpecialty.name_specialist_female
        ) || this.mainSpecialty.name
      }
      if (this.provider.specializations_expertise && this.provider.specializations_expertise.length > 0) {
        return this.provider.specializations_expertise[0].name
      }
      return !this.provider.alias ? '' : this.provider.alias
    }
  },
  methods: {
    async $_fetchProvider () {
      try {
        const { data } = await axios.get(`/providers/apikey/${this.apiKey}?widget_type=${this.widgetType}`)
        this.provider = data
        this.$_setBaseProviderURL(this.provider.region)
      } catch (error) {
        this.providerError = true
      }
    },
    $_setBaseProviderURL (region) {
      let baseURL = this.environment === 'prod'
        ? 'https://www.topdoctors'
        : `https://${this.environment}.topdoctors`

      switch (region) {
        case 'ar': baseURL += '.com.ar'; break
        case 'cl': baseURL += '.cl'; break
        case 'co': baseURL += '.com.co'; break
        case 'sa': baseURL += '.com.sa'; break
        default: baseURL += '.es'; break
      }
      this.baseProviderURL = baseURL
    },
    upperFirst (string) {
      return string.charAt(0).toUpperCase() + string.slice(1).toLowerCase()
    }
  }
}
</script>

